#aside {
	padding-top: 3em;
	padding-bottom: 40px;
	float: left;
	width: 300px;
	position: fixed;
	overflow-y: scroll;
	z-index: 1001;
	background: #f2f3f7;
	-webkit-transition: 0.5s;
	-o-transition: 0.5s;
	transition: 0.5s;
}
@media screen and (max-width: 768px) {
	#aside {
		width: 300px;
		-moz-transform: translateX(-300px);
		-webkit-transform: translateX(-300px);
		-ms-transform: translateX(-300px);
		-o-transform: translateX(-300px);
		transform: translateX(-300px);
		padding-top: 4em;
	}
}
#aside .author-img {
	width: 150px;
	height: 150px;
	margin: 0 auto;
	margin-bottom: 30px;
	-webkit-border-radius: 50%;
	-moz-border-radius: 50%;
	-ms-border-radius: 50%;
	border-radius: 50%;
}
#aside .position {
	display: block;
	margin-bottom: 2em;
	font-size: 12px;
	text-transform: uppercase;
}
#aside #logo {
	text-align: right;
	font-weight: 700;
	font-size: 22px;
	margin-bottom: 0.5em;
	display: block;
	width: 100%;
}
#aside #logo a {
	display: block;
	text-align: center;
	color: #000;
	padding-right: 0.3em;
}
#aside #main-menu {
	display: block;
	width: 100%;
}
#aside #main-menu .collapse {
	display: block !important;
}
#aside #main-menu ul {
	text-align: center;
	margin: 0;
	padding: 0;
}
@media screen and (max-width: 768px) {
	#aside #main-menu ul {
		margin: 0 0 2em 0;
	}
}
#aside #main-menu ul li {
	margin: 0 0 10px 0;
	padding: 0;
	list-style: none;
	font-size: 12px;
	font-weight: 500;
	text-transform: uppercase;
	letter-spacing: 1px;
}
#aside #main-menu ul li a {
	color: rgba(0, 0, 0, 0.7);
	text-decoration: none;
	position: relative;
	padding: 10px 0;
	font-family: "Quicksand", Arial, sans-serif;
	-webkit-transition: 0.3s;
	-o-transition: 0.3s;
	transition: 0.3s;
}
#aside #main-menu ul li a:after {
	content: "";
	position: absolute;
	height: 1px;
	bottom: 7px;
	left: 0;
	right: 0;
	background-color: #2c98f0;
	visibility: hidden;
	-webkit-transform: scaleX(0);
	-moz-transform: scaleX(0);
	-ms-transform: scaleX(0);
	-o-transform: scaleX(0);
	transform: scaleX(0);
	-webkit-transition: all 0.3s cubic-bezier(0.175, 0.885, 0.32, 1.275);
	-moz-transition: all 0.3s cubic-bezier(0.175, 0.885, 0.32, 1.275);
	-ms-transition: all 0.3s cubic-bezier(0.175, 0.885, 0.32, 1.275);
	-o-transition: all 0.3s cubic-bezier(0.175, 0.885, 0.32, 1.275);
	transition: all 0.3s cubic-bezier(0.175, 0.885, 0.32, 1.275);
}
#aside #main-menu ul li a:hover {
	text-decoration: none;
	color: black;
}
#aside #main-menu ul li a:hover:after {
	visibility: visible;
	-webkit-transform: scaleX(1);
	-moz-transform: scaleX(1);
	-ms-transform: scaleX(1);
	-o-transform: scaleX(1);
	transform: scaleX(1);
}
#aside #main-menu ul li.active a {
	color: #2c98f0;
}
#aside #main-menu ul li.active a:after {
	visibility: visible;
	-webkit-transform: scaleX(1);
	-moz-transform: scaleX(1);
	-ms-transform: scaleX(1);
	-o-transform: scaleX(1);
	transform: scaleX(1);
}
#aside .footer {
	font-size: 15px;
	text-align: center;
	font-weight: 400;
	color: rgba(0, 0, 0, 0.5);
	padding: 0 20px;
	width: 100%;
}
@media screen and (max-width: 768px) {
	#aside .footer {
		position: relative;
		bottom: 0;
	}
}
.author-img {
	background-size: cover;
	background-position: center center;
	background-repeat: no-repeat;
	position: relative;
}

#social-menu {
	padding-left: 67px;
	list-style-type: none;
}
#social-menu li {
	float: left;
}
#social-menu li a {
	display: block;
	color: grey;
	text-align: center;
	padding: 16px;
	text-decoration: none;
}
